  width: 100%;
  height: 2px;
  border-bottom: 2px solid #D5621D;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
}
.header-burger__line::after {
  content: "";
  position: absolute;
  top: 6px;
  left: 0;
  width: 100%;
