.gallery {
  margin-top: 25px;
  
  &-name {
    width: 244px;
    margin: 0 auto 40px;
    padding: 10px 10px 7px 10px;
    background: #fff;
    border-radius: 5px;
    font-family: 'Gotham Pro', sans-serif;
    font-size: 14px;
    font-weight: 700;
    line-height: 21px;
    letter-spacing: 0.05em;
    color: #210E09;
    text-align: center;
    text-transform: uppercase;
  }

  &-title {
    margin-bottom: 100px;
  }

  &-wrapper {
    margin-bottom: 150px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 24px;

    @media (max-width: 1399.98px) {
      justify-content: center;
    }
  }

  &-item {
    position: relative;

    a {
      position: relative;
      display: block;
      width: 100%;
      height: 100%;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgb(33, 14, 9, .9);
        opacity: 0;
        transition: opacity .3s ease-in-out;
      }

      &::after {
